import React from "react";
import { movieApi } from "../../api";
import HomePresenter from "./HomePresenter";

export default class HomeContainer extends React.Component {
  state = {
    nowPlaying: null,
    popular: null,
    upcoming: null,
    error: null,
    loading: true,
  };
  async componentDidMount(){
      try{
        const {data:{results:nowPlaying}} = await movieApi.nowPlaying()
        const {data:{results:upcoming}} = await movieApi.upcoming();
        const {data:{results:popular}} = await movieApi.popular();
        this.setState({
          nowPlaying,upcoming,popular
        })
      }catch{
        this.setState({error:"Can't find movies information"})
      }finally{
        this.setState({loading:false})
      }
  }
  render() {
    const { nowPlaying, popular, upcoming, error, loading } = this.state;
    console.log(this.state)
    return (
      <HomePresenter
        nowPlaying={nowPlaying}
        popular={popular}
        upcoming={upcoming}
        error={error}
        loading={loading}
      />
    );
  }
}

