import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import Section from "../../components/Section";
import Loader from "../../components/Loader";
const Container = styled.div``;

const TvPresenter = ({ topRated, popular, airingToday, error, loading }) =>loading?<Loader/>:
  <Container>
    {airingToday && airingToday.length > 0 && <Section title="Airing Today" >{airingToday.map(show=>show.name)}</Section>}
    {topRated && topRated.length > 0 && <Section title="Top Rated" >{topRated.map(show=>show.name)}</Section>}
    {popular && popular.length > 0 && <Section title="Popular" >{popular.map(show=>show.name)}</Section>}
  </Container>
;

TvPresenter.propTypes = {
  airingToday: PropTypes.array,
  topRated: PropTypes.array,
  popular: PropTypes.array,
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
};

export default TvPresenter;
